import React from 'react'
// import fetch from '../fetch/axios';
import '../../style/pageFrame.less';

class PageFrame extends React.Component {
  state = {
    height: 800
  }
  componentDidMount() {
    window.addEventListener('message', this.listenMsg.bind(this))
  }
  componentWillUnmount() {
    window.removeEventListener('message', this.listenMsg.bind(this))
  }
  listenMsg(event) {
    if (event.data?.height) {
      const height = event.data.height;
      this.setState({height: `${height}px`})
    }
  }
  
    constructor(props) {
      super(props);
      this.state = {
        loading: true,
      };
    }
  
    handleLoad = () => {
      this.setState({ loading: false });
    };
  
  render() {
    return (
      <div className="frame_page">
         {this.state.loading && <div>Loading...</div>}
        <iframe className='frame_loader'
          title='iframe'
          src={this.props.href}
          frameBorder="0"
          height={ this.state.height }
          scrolling="no"
          allowFullScreen 
          onLoad={this.handleLoad}
          >
        </iframe>
      </div>
    )
  }
}

export default PageFrame